@use "../../scss/"as *;

.background {
    background-color: #FFFFFF80;
    width: 100%;
    height: 100%;
    position: absolute;

    &-content-view {
        background-color: #FFFFFF;
        width: fit-content;
        height: fit-content;
        max-height: 60%;
        max-width: 50%;
        margin: auto;
        align-items: center;
        padding-top: 16px;
        padding-left: 16px;
        padding-right: 16px;
        padding-bottom: 16px;
        -ms-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        border-radius: 4px;
        position: absolute;
        top: 50%;
        left: 50%;
        border: 1px solid #f0f0f0;

        .close-view {
            display: flex;
            align-items: center;

            &-title {
                font-size: large;
            }
        }

        .list-request-view {
            .request-view {
                //background-color: aqua;
                margin-top: 16px;
            }

            .request-content {
                font-size: medium;
            }
        }

        .margin-top-4 {
            margin-top: 4px;
        }
    }
}